                                class="font-weight-bold">"thá»‘ng nháº¥t"</span> vá»›i káº¿t quáº£ tháº©m Ä‘á»‹nh. Báº¡n cháº¯c cháº¯c muá»‘n
                                tiáº¿p tá»¥c gá»­i?
                            </div>
                        </v-card-text>

                        <v-divider/>

